 <script>
 import { init, registerIndicator, registerOverlay } from 'klinecharts'
//  import './index.css'
 
 export default {
   mounted() {
    registerIndicator({
        name: 'Custom',
        figures: [
          { key: 'emoji' }
        ],
        calc: (kLineDataList) => {
          return kLineDataList.map(kLineData => ({ emoji: kLineData.close, text: fruits[Math.floor(Math.random() * 17)] }))
        },
        draw: ({
          ctx,
          barSpace,
          visibleRange,
          indicator,
          xAxis,
          yAxis
        }) => {
          const { from, to } = visibleRange

          ctx.font = barSpace.gapBar + 'px' + ' Helvetica Neue'
          ctx.textAlign = 'center'
          const result = indicator.result
          for (let i = from; i < to; i++) {
            const data = result[i]
            const x = xAxis.convertToPixel(i)
            const y = yAxis.convertToPixel(data.emoji)
            ctx.fillText(data.text, x, y)
          }
          return false
        }
      })

      registerOverlay({
        name: 'circle',
        needDefaultPointFigure: true,
        needDefaultXAxisFigure: true,
        needDefaultYAxisFigure: true,
        totalStep: 3,
        createPointFigures: ({ coordinates }) => {
          if (coordinates.length === 2) {
            const xDis = Math.abs(coordinates[0].x - coordinates[1].x)
            const yDis = Math.abs(coordinates[0].y - coordinates[1].y)
            const radius = Math.sqrt(xDis * xDis + yDis * yDis)
            return {
              precision:{10: 10},
              key: 'circle',
              type: 'circle',
              attrs: {
                ...coordinates[0],
                r: radius
              },
              styles: {
                style: 'stroke_fill'
              }
            }
          }
          return []
        }
      })
     this.chart = init('chart')
     this.chart.applyNewData(this.genData())
     this.chart.createIndicator('MA', true, { id: 'candle_pane' })
     this.chart.createOverlay('circle')
     //this.chart.createOverlay('priceLine')
    //  this.chart.createIndicator('VOL')
   },
 
   methods: {
    test(){
      this.bollbutton = !this.bollbutton
      if(this.bollbutton){this.chart.createIndicator('BOLL', true, { id: 'candle_pane' })}
      else {
        this.chart.removeIndicator( 'candle_pane' ,'BOLL')
      }

    },
    genData (timestamp = new Date().getTime(), length = 1000) {
        let basePrice = 5000
        timestamp = Math.floor(timestamp / 1000 / 60) * 60 * 1000 - length * 60 * 1000
        const dataList = []
        for (let i = 0; i < length; i++) {
          const prices = []
          for (let j = 0; j < 4; j++) {
            prices.push(basePrice + Math.random() * 60 - 30)
          }
          prices.sort()
          const open = +(prices[Math.round(Math.random() * 3)].toFixed(2))
          const high = +(prices[3].toFixed(2))
          const low = +(prices[0].toFixed(2))
          const close = +(prices[Math.round(Math.random() * 3)].toFixed(2))
          const volume = Math.round(Math.random() * 100) + 10
          const turnover = (open + high + low + close) / 4 * volume
          dataList.push({ timestamp, open, high,low, close, volume, turnover })

          basePrice = close
          timestamp += 60 * 1000
        }
        return dataList
      },
 
     setMainIndicator(name) {
        console.log(`Main indicator button clicked: ${name}`);
        chart.createIndicator(name, true, { id: 'candle_pane' })
     },
 
     setSubIndicator(name) {
        this.chart.createIndicator(name)
     }
   },
 
   data() {
     return {
       mainIndicators: ['MA', 'BOLL', 'Custom'],
       subIndicators: ['VOL', 'MACD',],
       bollbutton: false,
     }
   }
 }
 </script>

